import cx from 'classnames';
import React from 'react';
import { Link, useLocation, useHistory } from 'react-router-dom';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import HomeIcon from '@material-ui/icons/Home';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import {
  ClickAwayListener,
  Divider,
  useMediaQuery,
  Collapse,
} from '@material-ui/core';
import DarkModeToggle from './DarkModeToggle';

const useStyles = makeStyles(theme => ({
  root: {
    zIndex: theme.zIndex.drawer,
  },
  drawer: {
    // transition: theme.transitions.create('width', {
    //   easing: theme.transitions.easing.sharp,
    //   duration: theme.transitions.duration.leavingScreen,
    // }),
  },
  drawerShadow: {
    boxShadow: '0 0 0 9999px rgba(0, 0, 0, 0.5)',
    [theme.breakpoints.up('lg')]: {
      boxShadow: 'none',
    },
  },
  drawerHide: {
    boxShadow: 'none',
    marginLeft: '-1px',
    [theme.breakpoints.up('lg')]: {
      width: null,
    },
  },
  drawerHeader: {
    ...theme.mixins.toolbar,
  },
}));

export default function Nav({ open, onClose }) {
  const location = useLocation();
  const history = useHistory();
  const styles = useStyles();
  const theme = useTheme();
  const isLarge = useMediaQuery(theme.breakpoints.up('lg'));
  const prevIsLarge = React.useRef();
  const shouldCloseOnNav = React.useRef();

  shouldCloseOnNav.current = !isLarge && open;

  React.useEffect(() => {
    history.listen((location, action) => {
      if (action === 'PUSH' && shouldCloseOnNav.current) {
        onClose();
      }
    });
  }, [history, onClose]);

  React.useEffect(() => {
    if (open) {
      document.body.classList.add('overflow-hidden', 'lg:overflow-auto');
    } else {
      document.body.classList.remove('overflow-hidden', 'lg:overflow-auto');
    }
  }, [open]);

  // Close drawer when resizing down
  React.useEffect(() => {
    if (!isLarge && prevIsLarge.current) {
      onClose();
    }
    prevIsLarge.current = isLarge;
  }, [isLarge, onClose]);

  const clickAway = event => {
    if (!isLarge && open) {
      event.preventDefault();
      onClose();
    }
  };

  return (
    <ClickAwayListener
      onClickAway={clickAway}
      mouseEvent="onMouseUp"
      touchEvent="onTouchEnd">
      <Drawer
        anchor="left"
        className={cx(
          styles.root,
          styles.drawer,
          styles.drawerShadow,
          {
            [styles.drawerHide]: !open,
            'w-0': !open,
            'lg:w-56': open,
          },
          'relative flex-shrink-0',
        )}
        classes={{
          paper: cx(styles.drawer, 'flex flex-col', {
            [styles.drawerHide]: !open,
            'w-0': !open,
            'w-56': open,
          }),
        }}
        variant="permanent">
        <div className="flex-auto">
          <div
            className={cx(
              styles.drawerHeader,
              'flex flex-col justify-center px-5 whitespace-no-wrap',
            )}>
            <div className="sm:text-lg">Aftermarket Console</div>
            <div className="text-xs">v1.0.0</div>
          </div>
          <Divider />
          <List component="nav">
            <ListItem
              button
              component={Link}
              to="/"
              selected={location.pathname === '/'}>
              <ListItemIcon>
                <HomeIcon />
              </ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItem>
            <ListItem
              button
              component={Link}
              to="/auctions"
              selected={
                location.pathname === '/auctions' &&
                location.search.length === 0
              }>
              <ListItemIcon>
                <LocalOfferIcon />
              </ListItemIcon>
              <ListItemText primary="Auctions" />
            </ListItem>
            <Collapse
              in={location.pathname === '/auctions'}
              timeout="auto"
              unmountOnExit>
              <List disablePadding>
                <ListItem
                  button
                  component={Link}
                  to="/auctions?status=in_progress"
                  selected={location.search === '?status=in_progress'}>
                  <ListItemIcon>
                    <div />
                  </ListItemIcon>
                  <ListItemText primary="In progress" />
                </ListItem>
                <ListItem
                  button
                  component={Link}
                  to="/auctions?report=last30"
                  selected={location.search === '?report=last30'}>
                  <ListItemIcon>
                    <div />
                  </ListItemIcon>
                  <ListItemText primary="Last 30 days" />
                </ListItem>
              </List>
            </Collapse>
          </List>
        </div>
        <div className="p-4 lg:hidden">
          <DarkModeToggle />
        </div>
      </Drawer>
    </ClickAwayListener>
  );
}
