import React from 'react';
import {
  Avatar,
  Button,
  Chip,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
  Divider,
  TableContainer,
  Paper,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  IconButton,
  Popover,
} from '@material-ui/core';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import CreateIcon from '@material-ui/icons/Create';
import ForumIcon from '@material-ui/icons/Forum';
import HourglassEmptyIcon from '@material-ui/icons/HourglassEmpty';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import ReceiptIcon from '@material-ui/icons/Receipt';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';
import ShowChartIcon from '@material-ui/icons/ShowChart';
import VisibilityIcon from '@material-ui/icons/Visibility';
import {
  formatCurrency,
  formatNumber,
  formatDate,
  formatTime,
} from '../lib/formatters';
import useCountdown from '../lib/useCountdown';

function BidActions() {
  const [anchor, setAnchor] = React.useState(null);

  const handleClick = event => {
    setAnchor(event.currentTarget);
  };

  const handleClose = () => {
    setAnchor(null);
  };

  const open = Boolean(anchor);
  const id = open ? 'simple-popover' : undefined;

  return (
    <div>
      <IconButton aria-describedby={id} onClick={handleClick} size="small">
        <MoreVertIcon fontSize="small" />
      </IconButton>
      <Popover
        id={id}
        open={open}
        anchorEl={anchor}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}>
        <List dense>
          <ListItem button>
            <ListItemText primary="Mark as invalid" />
          </ListItem>
          <ListItem button>
            <ListItemText primary="Mark as fraud" />
          </ListItem>
        </List>
      </Popover>
    </div>
  );
}

export default function Auction({ auction, bids }) {
  const countdown = useCountdown(auction && auction.end_time_stamp);
  const isClosed = auction && auction.end_time_stamp < Date.now();

  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="pt-8 pb-6">
      <div className="px-4 lg:px-8">
        <Typography className="mb-6 break-words" variant="h4">
          <span className="mr-4">{auction.domain_name_utf}</span>
          <Chip color="primary" label="Expired domain" />
        </Typography>
        <Divider />
        <div className="flex items-center lg:justify-start my-3">
          <Button
            className="p-3"
            classes={{ label: 'flex-col normal-case', startIcon: 'mr-0 mb-2' }}
            startIcon={<RemoveCircleOutlineIcon />}
            variant="text">
            Close auction
          </Button>
          <Button
            className="p-3"
            classes={{ label: 'flex-col normal-case', startIcon: 'mr-0 mb-2' }}
            startIcon={<CreateIcon />}
            variant="text">
            Add a note
          </Button>
        </div>
        <Divider />
        <div className="mt-6 mb-2">
          <Typography variant="overline">Details</Typography>
        </div>
        <List disablePadding>
          <ListItem disableGutters>
            <ListItemAvatar>
              <Avatar>
                <ShowChartIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={`Current price: ${formatCurrency(
                auction.current_price,
              )}`}
              secondary={`Starting price: ${formatCurrency(
                auction.start_price,
              )}`}
            />
          </ListItem>
          <ListItem disableGutters>
            <ListItemAvatar>
              <Avatar>
                <ForumIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={`${auction.bids} bids`}
              secondary={`${auction.bidders} bidders`}
            />
          </ListItem>
          <ListItem disableGutters>
            <ListItemAvatar>
              <Avatar>
                <HourglassEmptyIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={
                isClosed ? 'Closed' : `Time remaining: ${countdown || '…'}`
              }
              secondary={`Auction end${isClosed ? 'ed' : 's'} on ${formatDate(
                auction.end_time_stamp,
                'long',
              )} at ${formatTime(auction.end_time_stamp)}`}
            />
          </ListItem>
          <ListItem disableGutters>
            <ListItemAvatar>
              <Avatar>
                <VisibilityIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={`${
                auction.visitors > 0 ? formatNumber(auction.visitors) : 0
              } views`}
            />
          </ListItem>
          <ListItem disableGutters>
            <ListItemAvatar>
              <Avatar>
                <ReceiptIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={`Registered on ${formatDate(
                auction.registration_date,
                'long',
              )}`}
            />
          </ListItem>
          <ListItem disableGutters>
            <ListItemAvatar>
              <Avatar>
                <AttachMoneyIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={`${formatCurrency(auction.revenue)} revenue`}
            />
          </ListItem>
        </List>
      </div>
      <div className="p-4 lg:px-8">
        <div className="my-4">
          <Typography variant="overline">Notes</Typography>
        </div>
        <Divider />
        <Typography className="my-4 max-w-xl" variant="body2">
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos
          blanditiis tenetur unde suscipit, quam beatae rerum inventore
          consectetur, neque doloribus, cupiditate numquam dignissimos laborum
          fugiat deleniti? Eum quasi quidem quibusdam.
        </Typography>
        <Typography className="mb-4" variant="caption" display="block">
          Jan 28, 2020 at 10:01 AM
        </Typography>
        <Divider />
      </div>
      <div className="px-4 lg:px-8">
        <div className="mt-4 mb-4">
          <Typography variant="overline">Bids</Typography>
        </div>
        <TableContainer component={Paper} square>
          <Table className="whitespace-no-wrap" aria-label="Bids">
            <TableHead>
              <TableRow>
                <TableCell>Bid ID</TableCell>
                <TableCell>Account ID</TableCell>
                <TableCell>Amount</TableCell>
                <TableCell>Status</TableCell>
                <TableCell>Time</TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {bids.map(bid => (
                <TableRow key={bid.bid_id}>
                  <TableCell>{bid.bid_id}</TableCell>
                  <TableCell>{bid.account_id}</TableCell>
                  <TableCell>{formatCurrency(bid.bid_amount_usd)}</TableCell>
                  <TableCell>{bid.bid_status}</TableCell>
                  <TableCell>
                    {new Date(bid.timestamp).toLocaleString()}
                  </TableCell>
                  <TableCell align="right">
                    <BidActions bid={bid} />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </div>
  );
}
