import axios from 'axios';
import cx from 'classnames';
import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import CSSBaseline from '@material-ui/core/CssBaseline';
import { Alert } from '@material-ui/lab';
import { ThemeProvider } from '@material-ui/core/styles';
import getMUITheme from '../lib/getMUITheme';
import Auction from './Auction';
import AuctionIndex from './AuctionIndex';
import Dashboard from './Dashboard';
import Nav from './Nav';
import Header from './Header';
import useDarkMode from '../lib/useDarkMode';
import { useMediaQuery } from '@material-ui/core';
import { Store } from '../lib/store';

const messages = {
  loadError: 'Failed to load console data. Try whitelisting your IP address.',
};

function App() {
  const [darkMode] = useDarkMode();
  const theme = React.useMemo(() => getMUITheme(darkMode ? 'dark' : 'light'), [
    darkMode,
  ]);
  const isLarge = useMediaQuery(theme.breakpoints.up('lg'));
  const {
    state: { auctions, bids, loadError },
    dispatch,
  } = React.useContext(Store);
  const [navOpen, setNavOpen] = React.useState(false);

  React.useEffect(() => {
    if (isLarge) {
      setNavOpen(true);
    }
  }, [isLarge]);

  React.useEffect(() => {
    Promise.all([
      axios.get(`${process.env.PUBLIC_URL}/auctions.json`),
      axios.get(`${process.env.PUBLIC_URL}/bids.json`),
    ])
      .then(responses => {
        if (responses.some(r => r.data.status === 'error')) {
          console.error(messages.loadError);
          dispatch({ type: 'FETCH_ERROR' });
        } else {
          dispatch({
            type: 'FETCH_DATA',
            auctions: responses[0].data.auctions,
            bids: responses[1].data.bids,
          });
        }
      })
      .catch(error => {
        console.error(error);
        dispatch({ type: 'FETCH_ERROR' });
      });
  }, [dispatch]);

  return (
    <ThemeProvider theme={theme}>
      <CSSBaseline />
      <Router
        basename={
          process.env.NODE_ENV === 'production' ? '/aftermarket-console' : '/'
        }>
        <Nav open={navOpen} onClose={() => setNavOpen(false)} />
        <div className={cx({ 'lg:ml-56': navOpen })}>
          {loadError && (
            <Alert className="rounded-none" severity="error" variant="filled">
              {messages.loadError}
            </Alert>
          )}
          <Header onToggleNav={() => setNavOpen(!navOpen)} />
          <Route exact path="/">
            <Dashboard />
          </Route>
          <Route path="/auctions">
            {auctions && <AuctionIndex auctions={auctions} />}
          </Route>
          <Route
            path="/auction/:id"
            render={({ match }) => {
              const auction =
                auctions &&
                auctions.find(a => a.auction_id === parseInt(match.params.id));
              if (auction) {
                return <Auction auction={auction} bids={bids} />;
              }
            }}></Route>
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
