import { createMuiTheme } from '@material-ui/core/styles';

export default function getMUITheme(paletteType = 'light') {
  return createMuiTheme({
    breakpoints: {
      values: {
        sm: 640,
        md: 768,
        lg: 1024,
        xl: 1280,
      },
    },
    palette: {
      type: paletteType,
      secondary: {
        light: '#f14c2e',
        main: '#de3723',
      },
      primary: {
        main: '#8cc1c1',
        dark: '#669999',
      },
      error: {
        main: '#fc6666',
      },
      warning: {
        main: '#f59000',
      },
      info: {
        main: '#3281e8',
      },
      success: {
        main: '#74cccc',
      },
    },
    spacing: 8,
    typography: {
      // Use system font
      fontFamily: [
        '-apple-system',
        'BlinkMacSystemFont',
        '"Segoe UI"',
        'Roboto',
        '"Helvetica Neue"',
        'Arial',
        'sans-serif',
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"',
      ].join(','),
    },
  });
}
