import cx from 'classnames';
import { ExportToCsv } from 'export-to-csv';
import moment from 'moment';
import React from 'react';
import MomentUtils from '@date-io/moment';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TablePagination,
  TableSortLabel,
  Link,
  Chip,
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  Tooltip,
  Fab,
  FormLabel,
  Paper,
  Popper,
  ClickAwayListener,
  Fade,
} from '@material-ui/core';
import SaveAltIcon from '@material-ui/icons/SaveAlt';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import { Link as RouterLink, useHistory, useLocation } from 'react-router-dom';
import { formatCurrency, formatDate } from '../lib/formatters';
import Countdown from './Countdown';
import sortAndFilterRows from '../lib/sortAndFilterRows';

const exporter = new ExportToCsv({
  filename: 'report',
  useKeysAsHeaders: true,
});

function URLParamsToObject(entries) {
  let result = {};
  for (let entry of entries) {
    // each 'entry' is a [key, value] tupple
    const [key, value] = entry;
    result[key] = value;
  }
  return result;
}

const columns = [
  {
    key: 'domain_name_utf',
    label: 'Domain',
    className: 'pl-4 sm:pl-6',
  },
  {
    key: 'current_price',
    label: 'Current price',
    formatter: formatCurrency,
  },
  {
    key: 'start_price',
    label: 'Starting price',
    formatter: formatCurrency,
  },
  {
    key: 'bids',
    label: 'Bids',
  },
  {
    key: 'visitors',
    label: 'Views',
    formatter: input => Math.max(0, input),
  },
  {
    key: 'end_time_stamp',
    label: 'Time remaining',
    formatter: input => <Countdown to={input} />,
  },
  {
    key: 'start_time_stamp',
    label: 'Start date',
    formatter: formatDate,
  },
  {
    key: 'end_time_stamp',
    label: 'End date',
    formatter: formatDate,
  },
  {
    key: 'registration_date',
    label: 'Registered',
    formatter: formatDate,
  },
];

function formatStatusFacet(status) {
  if (status === 'in_progress') {
    return 'In progress';
  } else if (status === 'closed') {
    return 'Closed';
  }
}

function formatTypeFacet(type) {
  switch (type) {
    case 'expired':
      return 'Expired';
    case 'portfolio':
      return 'Portfolio';
    case 'user':
      return 'User';
    default:
      return;
  }
}

function formatDateFacets(from, to) {
  if (from && to) {
    return `${formatDate(from)} – ${formatDate(to)}`;
  } else if (from) {
    return `After ${formatDate(from)}`;
  } else if (to) {
    return `Before ${formatDate(to)}`;
  }
}

function Facet({ children, isDatePickerOpen, label, onDelete, name, value }) {
  const [anchor, setAnchor] = React.useState(null);
  const open = Boolean(anchor);
  return (
    <div className="inline-block">
      <Chip
        aria-describedby={name}
        className="mr-2 mb-3 text-sm h-8"
        color={value ? 'primary' : 'default'}
        label={value ? `${label}: ${value}` : label}
        onClick={event => setAnchor(event.currentTarget)}
        onDelete={value ? onDelete : null}
        variant="outlined"
      />
      <Popper
        id={name}
        open={open}
        anchorEl={anchor}
        className="mt-3"
        placement="bottom-start"
        transition>
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={300}>
            <Paper className="inline-block">
              <ClickAwayListener
                onClickAway={() => !isDatePickerOpen && setAnchor(null)}
                mouseEvent="onMouseUp">
                <Paper>{children}</Paper>
              </ClickAwayListener>
            </Paper>
          </Fade>
        )}
      </Popper>
    </div>
  );
}

export default function AuctionIndex({ auctions = [] }) {
  const history = useHistory();
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const filter = React.useMemo(
    () => URLParamsToObject(new URLSearchParams(location.search)),
    [location.search],
  );
  const [rows, setRows] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(25);
  const [order, setOrder] = React.useState('asc');
  const [orderBy, setOrderBy] = React.useState('end_time_stamp');
  const [isDatePickerOpen, setDatePickerOpen] = React.useState(false);

  const setFacet = (facet, value) => {
    if (!value) {
      params.delete(facet);
    } else {
      params.set(facet, value);
    }
    history.replace({
      ...location,
      search: `?${params.toString()}`,
    });
  };

  const datePickerProps = facetName => ({
    autoOk: true,
    clearable: true,
    className: 'mr-4 mb-4 block sm:inline-block w-auto',
    disableToolbar: true,
    format: 'LL',
    initialFocusedDate: new Date(),
    inputVariant: 'outlined',
    onOpen: () => setDatePickerOpen(true),
    onChange: date =>
      setFacet(facetName, date ? date.format('YYYY-MM-DD') : null),
    onClose: () => setDatePickerOpen(false),
    value: filter[facetName] || null,
  });

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleRequestSort = property => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  React.useEffect(() => {
    const rows = sortAndFilterRows(auctions, order, orderBy, filter);
    setRows(rows);
  }, [auctions, order, orderBy, filter]);

  React.useEffect(() => {
    if (filter.report && filter.report === 'last30') {
      setFacet('report', null);
      setFacet(
        'end_from',
        moment()
          .subtract(30, 'days')
          .format('YYYY-MM-DD'),
      );
    }
  });

  return (
    <div className="pb-24">
      <Tooltip title="Export to CSV" enterDelay={300}>
        <Fab
          aria-label="Export to CSV"
          className="fixed bottom-0 right-0 m-6 md:m-8 z-10"
          color="primary"
          onClick={() => {
            exporter.generateCsv(rows);
          }}>
          <SaveAltIcon />
        </Fab>
      </Tooltip>
      <div className="mt-4 mx-3 flex items-end lg:items-center">
        <div className="flex-auto">
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <Facet
              isDatePickerOpen={isDatePickerOpen}
              label="Status"
              name="status"
              onDelete={() => setFacet('status', null)}
              value={formatStatusFacet(filter.status)}>
              <FormControl className="p-4" component="fieldset">
                <FormLabel className="mb-2">Status</FormLabel>
                <RadioGroup
                  aria-label="Status"
                  name="status"
                  value={filter.status || ''}
                  onChange={event => setFacet('status', event.target.value)}>
                  <FormControlLabel
                    value=""
                    control={<Radio size="small" />}
                    label="Any"
                  />
                  <FormControlLabel
                    value="closed"
                    control={<Radio size="small" />}
                    label="Closed"
                  />
                  <FormControlLabel
                    value="in_progress"
                    control={<Radio size="small" />}
                    label="In progress"
                  />
                </RadioGroup>
              </FormControl>
            </Facet>
            <Facet
              isDatePickerOpen={isDatePickerOpen}
              label="Type"
              name="type"
              onDelete={() => setFacet('type', null)}
              value={formatTypeFacet(filter.type)}>
              <FormControl className="p-4" component="fieldset">
                <FormLabel className="mb-2">Type</FormLabel>
                <RadioGroup
                  aria-label="Type"
                  name="type"
                  value={filter.type || ''}
                  onChange={event => setFacet('type', event.target.value)}>
                  <FormControlLabel
                    value=""
                    control={<Radio size="small" />}
                    label="Any"
                  />
                  <FormControlLabel
                    value="expired"
                    control={<Radio size="small" />}
                    label="Expired"
                  />
                  <FormControlLabel
                    value="portfolio"
                    control={<Radio size="small" />}
                    label="Portfolio"
                  />
                  <FormControlLabel
                    value="user"
                    control={<Radio size="small" />}
                    label="User"
                  />
                </RadioGroup>
              </FormControl>
            </Facet>
            <Facet
              isDatePickerOpen={isDatePickerOpen}
              label="Start date"
              name="start_date"
              onDelete={() => {
                setFacet('start_from', null);
                setFacet('start_to', null);
              }}
              value={formatDateFacets(filter.start_from, filter.start_to)}>
              <div className="inline-block p-5">
                <FormLabel className="mb-6 block">Start date</FormLabel>
                <DatePicker {...datePickerProps('start_from')} label="From" />
                <DatePicker
                  {...datePickerProps('start_to')}
                  label="To"
                  minDate={filter.start_from}
                />
              </div>
            </Facet>
            <Facet
              isDatePickerOpen={isDatePickerOpen}
              label="End date"
              name="end_date"
              onDelete={() => {
                setFacet('end_from', null);
                setFacet('end_to', null);
              }}
              value={formatDateFacets(filter.end_from, filter.end_to)}>
              <div className="p-5">
                <FormLabel className="mb-6 block">End date</FormLabel>
                <DatePicker {...datePickerProps('end_from')} label="From" />
                <DatePicker
                  {...datePickerProps('end_to')}
                  label="To"
                  minDate={filter.end_from}
                />
              </div>
            </Facet>
            <Facet
              isDatePickerOpen={isDatePickerOpen}
              label="Registered date"
              name="registered_date"
              onDelete={() => {
                setFacet('registered_from', null);
                setFacet('registered_to', null);
              }}
              value={formatDateFacets(
                filter.registered_from,
                filter.registered_to,
              )}>
              <div className="p-5">
                <FormLabel className="mb-6 block">Registered date</FormLabel>
                <DatePicker
                  {...datePickerProps('registered_from')}
                  label="From"
                />
                <DatePicker
                  {...datePickerProps('registered_to')}
                  label="To"
                  minDate={filter.registered_from}
                />
              </div>
            </Facet>
          </MuiPickersUtilsProvider>
        </div>
      </div>
      <TableContainer>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              {columns.map((col, index) => (
                <TableCell
                  key={`${col.key}${index}`}
                  className={cx(col.className, 'whitespace-no-wrap')}>
                  <TableSortLabel
                    active={orderBy === col.key}
                    direction={orderBy === col.key ? order : 'asc'}
                    onClick={() => handleRequestSort(col.key)}>
                    {col.label}
                    {orderBy === col.key ? (
                      <span className="hidden">
                        {order === 'desc'
                          ? 'sorted descending'
                          : 'sorted ascending'}
                      </span>
                    ) : null}
                  </TableSortLabel>
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map(row => (
                <TableRow key={row.domain_name_utf}>
                  {columns.map((col, index) => (
                    <TableCell
                      className={cx(col.className, 'whitespace-no-wrap')}
                      component="th"
                      key={`${col.key}${index}`}
                      scope="row">
                      {index === 0 ? (
                        <Link
                          color="textSecondary"
                          component={RouterLink}
                          to={`/auction/${row.auction_id}`}>
                          {col.formatter
                            ? col.formatter(row[col.key])
                            : row[col.key]}
                        </Link>
                      ) : col.formatter ? (
                        col.formatter(row[col.key])
                      ) : (
                        row[col.key]
                      )}
                    </TableCell>
                  ))}
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      {rows.length > 0 && (
        <TablePagination
          rowsPerPageOptions={[25, 50, 100]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      )}
    </div>
  );
}
