import cx from 'classnames';
import _orderBy from 'lodash/orderBy';
import React from 'react';
import {
  InputBase,
  ListItemText,
  List,
  ListItem,
  Paper,
} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import { makeStyles } from '@material-ui/core/styles';
import { useAutocomplete } from '@material-ui/lab';
import { Store } from '../lib/store';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  search: {
    backgroundColor:
      theme.palette.type === 'dark'
        ? theme.palette.grey[800]
        : theme.palette.common.white,
    border:
      theme.palette.type === 'light'
        ? `1px solid ${theme.palette.grey[300]}`
        : null,
    '&:hover': {
      backgroundColor:
        theme.palette.type === 'dark' ? theme.palette.grey[700] : null,
    },
  },
  searchIcon: {
    width: theme.spacing(7),
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    width: '100%',
    transition: theme.transitions.create('width'),
    [theme.breakpoints.up('sm')]: {
      width: 180,
      '&:focus': {
        width: 220,
      },
    },
  },
  option: {
    cursor: 'pointer',
    '&[data-focus="true"]': {
      backgroundColor: theme.palette.action.selected,
    },
  },
  listContainer: {
    maxHeight: 'calc(100vh - 100%)',
    top: 'calc(100% + 1px)',
    [theme.breakpoints.up('lg')]: {
      height: 'auto',
      maxHeight: '50vh',
    },
  },
}));

export default function Search() {
  const styles = useStyles();
  const {
    state: { auctions },
  } = React.useContext(Store);
  const history = useHistory();
  const {
    getRootProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
  } = useAutocomplete({
    disableOpenOnFocus: true,
    getOptionLabel: option => option.title,
    id: 'main-search',
    onChange: (event, value) => value && history.push(`/auction/${value.id}`),
    // open: true, // useful for debugging
    options: _orderBy(
      auctions.map(a => ({
        title: a.domain_name_utf,
        id: a.auction_id,
      })),
      'title',
    ),
  });
  return (
    <div
      className={cx(
        styles.search,
        'lg:relative rounded-lg sm:rounded w-full lg:mr-1 sm:w-auto sm:ml-1',
      )}
      {...getRootProps()}>
      <div className="flex">
        <div
          className={cx(
            styles.searchIcon,
            'pointer-events-none flex items-center justify-center order-last sm:order-first',
          )}>
          <SearchIcon />
        </div>
        <InputBase
          placeholder="Search…"
          classes={{
            root: cx(styles.inputRoot, 'flex-auto py-2 sm:py-1 pl-16 sm:pl-0'),
            input: styles.inputInput,
          }}
          inputProps={{ 'aria-label': 'search', ...getInputProps() }}
        />
      </div>
      {groupedOptions.length > 0 ? (
        <Paper
          className={cx(
            styles.listContainer,
            'absolute right-0 left-0 overflow-x-hidden overflow-y-scroll',
          )}
          square>
          <List {...getListboxProps()}>
            {groupedOptions.slice(0, 100).map((option, index) => (
              <ListItem
                className={styles.option}
                {...getOptionProps({ option, index })}>
                <ListItemText primary={option.title} />
              </ListItem>
            ))}
          </List>
        </Paper>
      ) : null}
    </div>
  );
}
