const filterFacetFns = {
  status: (row, state) => {
    switch (state) {
      case 'in_progress':
        return row.end_time_stamp > Date.now();
      case 'closed':
        return row.end_time_stamp <= Date.now();
      default:
        return true;
    }
  },
  type: (row, state) => {
    switch (state) {
      case 'expired':
        return row.type === 0;
      case 'portfolio':
        return row.type === 2;
      case 'user':
        return row.type === 1;
      default:
        return true;
    }
  },
  start_from: (row, state) =>
    state ? row.start_time_stamp >= new Date(state).valueOf() : true,
  start_to: (row, state) =>
    state ? row.start_time_stamp <= new Date(state).valueOf() : true,
  end_from: (row, state) =>
    state ? row.end_time_stamp >= new Date(state).valueOf() : true,
  end_to: (row, state) =>
    state ? row.end_time_stamp <= new Date(state).valueOf() : true,
  registered_from: (row, state) =>
    state ? row.registered_time_stamp >= new Date(state).valueOf() : true,
  registered_to: (row, state) =>
    state ? row.registered_time_stamp <= new Date(state).valueOf() : true,
};

function desc(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function stableSort(array, cmp) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = cmp(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

function getSorting(order, orderBy) {
  return order === 'desc'
    ? (a, b) => desc(a, b, orderBy)
    : (a, b) => -desc(a, b, orderBy);
}

export default function sortAndFilterRows(rows, order, orderBy, filter) {
  return stableSort(rows, getSorting(order, orderBy)).filter(row =>
    Object.keys(filterFacetFns).every(facet =>
      filterFacetFns[facet](row, filter[facet]),
    ),
  );
}
